.catch-section {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-image: url('./image/background.jpeg');
    background-size: cover;
    background-position: center;
    border: 20px solid white;
}

.catch-me-container {
    position: absolute;
    cursor: pointer;
    transition: transform 0.5s ease-in-out, top 0.5s ease-in-out, left 0.5s ease-in-out;
    will-change: transform, top, left;
}

.catch-me-container:hover {
    transform: scale(1.2) rotate(5deg); /* Slight tilt when hovering */
}

.catch-box {
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.su-catch-image {
    width: 200px;
    height: auto;
    transition: transform 0.5s ease;
}

.catch-image.fly {
    animation: flyEffect 1s ease-in-out infinite; /* Activate fly animation */
}

@keyframes flyEffect {
    0% { transform: scale(1) rotate(0deg); }
    50% { transform: scale(1.1) rotate(10deg); }
    100% { transform: scale(1) rotate(0deg); }
}

.hover-text {
    position: absolute;
    bottom: -30px;
    color: #111111;
    font-size: 1.5rem;
    font-weight: bold;
}

.su-modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.5s ease-in-out;
}

.su-modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 30px;
    max-width: 500px;
    width: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    text-align: center;
    animation: fadeIn 0.3s forwards;
}

/* Close Button */
.vb-close {
    padding: 5px 10px; /* Smaller padding */
    position: absolute; /* Positioning */
    top: 10px; /* Distance from the top */
    right: 10px; /* Distance from the right */
    background-color:#2962FF;
    color: white; /* Color matching the modal theme */
    border-radius: 10px;
}

.vb-close:hover {
    padding: 5px 10px;
    background-color: #e31937;
    color: white;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

/* Keyframe Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Optional fade-out animation when closing the modal */
.su-modal.fade-out {
    animation: fadeOut 0.5s ease-in-out forwards;
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.su-superman-theme {
    background-color: #0047ab; /* Superman blue */
    color: white; /* Superman yellow */
    border: 5px solid #e31937; /* Superman red */
}



.su-profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 10px auto;
    display: block;
}

.su-contact-info {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
}


.su-contact-button {
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    margin: 5px 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

.su-contact-button:hover {
    background-color: #0056b3; /* Darker shade for hover effect */
}
.su-icon {
    margin-right: 10px; /* Space between icon and text */
    font-size: 20px; /* Adjust icon size */
}

.f-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    transition: opacity 0.3s ease; /* Fade-in transition */
}

.f-modal-content {
    background-color: #f4f4f9; /* Soft modern background */
    padding: 40px;
    border-radius: 15px;
    max-width: 450px;
    width: 90%; /* Responsive width */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    text-align: center;
    transition: transform 0.3s ease; /* Smooth animation */
    transform: scale(1.05);
}

.f-modal-content h2 {
    margin-bottom: 20px;
    color: #333333;
    font-size: 1.8rem;
    font-weight: bold;
}

.f-modal-content p {
    color: #777777;
    font-size: 1rem;
    margin-bottom: 10px;
}

.f-modal-content textarea {
    width: 100%;
    padding: 15px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 20px;
    resize: none;
    outline: none;
    transition: border 0.2s ease-in-out;
}

.f-modal-content textarea:focus {
    border-color: #2962FF;
}

.f-modal-submit-button {
    background-color: #2962FF;
    color: white;
    padding: 12px 25px;
    font-size: 1rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.f-modal-submit-button:hover {
    background-color: #00337a;
}

.f-modal-close-button {
    background-color: #ff0000;
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 15px;
    transition: background-color 0.3s ease;
}

.f-modal-close-button:hover {
    background-color: #cc0000;
}

.feedback-anonymous-text {
    color: #555555;
    font-size: 0.9rem;
    margin-bottom: 15px;
    font-style: italic;
}

.feedback-submitted-message {
    color: #2962FF;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 20px;
}

/* Add subtle entry animation */
.f-modal-overlay {
    opacity: 0;
    animation: fadeIn 0.3s forwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.f-modal-content {
    transform: scale(0.95);
    animation: scaleUp 0.3s forwards;
}

@keyframes scaleUp {
    0% {
        transform: scale(0.95);
    }
    100% {
        transform: scale(1);
    }
}

  